import React, {Component} from 'react';
import Projects from '../components/project'
import axios from 'axios';

class ProjectContainer extends Component {

    state = {
        projects: null
    }
    projectComponent;

    componentDidMount() {
        if(!this.state.projects) {
            axios.get('https://portfolio-99b6a.firebaseio.com/achievements.json')
                .then((result) => {
                        // alert(result)
                        console.log(result)
                        this.setState(this.state.projects = result.data)
                    }
                )
        }
    }

    render() {
        if(this.state.projects) {
             this.projectComponent = this.state.projects.map(
                (project, index) => {
                    // alert(project.icon)
                    return <Projects
                        key={index}
                        id={index}
                        logo="sample"
                        name={project.name}
                        des={project.description}
                        icon={project.icon}

                    />
                }
            );
        }
        return (
            <div data-target="services" id="services" style={{background: "#aaa"}}>
                <div className="container">
                    <div className="jarallax  w3ls-services w3ls-section principles-grids principles-grids1">
                        <h3 className="w3l-head aos-init aos-animate" data-aos="zoom-in"><span
                            className="title-head">A</span>chievements</h3>
                        <div className="abt-btm agileits w3layouts aos-init aos-animate">

                            {this.projectComponent}


                        </div>
                    </div>
                </div>
            </div>
        )
    }

}


export default ProjectContainer