import React from 'react';

const hobby = (props)=>{

    var classid = props.id % 5;
    // alert(classid)
    var style = {}

    if(classid===0)
        style = {border: '2px solid #ff4f81'}
    else if(classid===1)
        style = {border: '2px solid #1cc7d0'}
    else if(classid===2)
        style = {border: '2px solid #8e43e7'}
    else if(classid===3)
        style = {border: '2px solid #1cc7d0'}
    else if(classid===4)
        style = {border: '2 px solid #ed1b2e'}
    else
        style = {border: '2px solid #7fbb00'}

    return(
        <div className="col-md-4 hobbies-grid1">
            <span className={props.icon} style={style}></span>
            <h5>{props.name}</h5>
        </div>
    )

}

export default hobby