import React from 'react'

const Projects  = (props)=>{

    var classid = props.id % 6;
    // alert(classid)
    var style = {}
    var style2={}
    if(classid===0){
        style = {background: '#ff4f81'};
        style2 = {borderBottom:'4px solid #ff4f81'}
    }

    else if(classid===1) {
        style = {background: '#1cc7d0'}
        style2 = {borderBottom:'4px solid #1cc7d0'}
    }
    else if(classid===2) {
        style = {background: '#8e43e7'}
        style2 = {borderBottom:'4px solid #8e43e7'}
    }
    else if(classid===3) {
        style = {background: '#2828FF'}
        style2 = {borderBottom:'4px solid #1cc7d0'}
    }
    else if(classid===4) {
        style = {background: '#ed1b2e'}
        style2 = {borderBottom:'4px solid #ed1b2e'}
    }
    else {
        style = {background: '#bb13a7'}
        style2 = {borderBottom:'4px solid #bb13a7'}
    }

    return(
        <div className="col-md-4 col-sm-12 agileits elite-services w3layouts bottom-gds projects" data-aos="fade-up" style={style2}>
            <div className="bott-img agileits w3layouts">
                <div className="icon-holder agileits w3layouts" style={style}>
                    <span className={props.icon + ' agileits w3layouts service-icon'} aria-hidden="true"></span>
                </div>
                <h4 className="mission agileits w3layouts">{props.name}</h4>
                <div className="description agileits w3layouts">
                    <p>{props.des}</p>

                </div>
            </div>
        </div>
    )
};

export default Projects