


import React from 'react';

const programSkill = (props)=>{
    var classid = props.id % 5;
    // alert(classid)
    var style = {}

    if(classid==0)
        style = {background: '#ff4f81'}
    else if(classid==1)
        style = {background: '#1cc7d0'}
    else if(classid==2)
        style = {background: '#8e43e7'}
    else if(classid==3)
        style = {background: '#1cc7d0'}
    else if(classid==4)
        style = {background: '#ed1b2e'}
    else
        style = {background: '#7fbb00'}




    return(

        <div className="skillbar clearfix " data-percent={props.percent + "%"}>
            <div className="skillbar-title" style={style}><span>{props.name}</span></div>
            <div className="skillbar-bar" style={style}></div>
            <div className="skill-bar-percent">{props.percent + "%"}</div>
        </div>

    )
};


export default programSkill