import React,{Component} from 'react';
import Skills from '../components/skill'
import Hobby from '../components/hobby'
import ProgramSkill from "../components/programSkill";
import axios from 'axios'
import $ from "jquery";
// const skillConatainer = () => {
//
//     return (

//
//     )
// };

class skillContainer extends Component{
    state  = {
        skills:null,
        hobbies:null,
        programSkill:null
    }

    animateSkills () {

        // alert('sdf')
        $(document).ready(function() {
            $('.skillbar').each(function() {
                $(this).find('.skillbar-bar').animate({
                    width: $(this).attr('data-percent')
                }, 6000);
            });
        });
    }
    componentDidMount() {

        if(!this.state.skills){
            // alert('sdfsd')
            axios.get('https://portfolio-99b6a.firebaseio.com/skills.json')
                .then((result)=>{
                    this.setState(this.state.skills = result.data);
                    this.animateSkills();
                })
        }
        if(!this.state.hobbies){
            // alert('sdfsd')
            axios.get('https://portfolio-99b6a.firebaseio.com/hobbies.json')
                .then((result)=>{
                    this.setState(this.state.hobbies = result.data);
                    this.animateSkills();
                })
        }
        if(!this.state.hobbies){
            // alert('sdfsd')
            axios.get('https://portfolio-99b6a.firebaseio.com/programSkill.json')
                .then((result)=>{
                    this.setState(this.state.programSkill = result.data);
                    this.animateSkills();
                })
        }

    }

    render() {
        // this.getData();
        var skillComponent,hobbyComponent,programComponent;
        if(this.state.skills){
             skillComponent  = this.state.skills.map(
                (skill , index)=>{
                    return <Skills
                        key={index}
                        id={index}
                        name={skill.name}
                        percent={skill.percent}
                    />
                }
            )
        }
        if(this.state.hobbies){
            hobbyComponent = this.state.hobbies.map(
                (hobby ,index)=>{
                    return <Hobby
                        key={index}
                        id={index}
                        name={hobby.name}
                        icon={hobby.icon}

                    />
                }
            )
        }
        if(this.state.programSkill){
            programComponent = this.state.programSkill.map(
                (skill ,index)=>{
                    return <ProgramSkill
                        key={index}
                        id={index}
                        name={skill.name}
                        percent={skill.percent}

                    />
                }
            )
        }

        return(
            <div className="skills-agileinfo" id="skills">
                <div className="container">
                    <h3 className="text-center">My skills</h3>
                    <div className="modal-spa">
                        <div className="col-md-6 bar-grids bargrids-left">
                            {skillComponent}
                            <p className="p1">A skill set is a particular category of knowledge,
                                abilities, and experience necessary to perform a job.You can job-hunt by matching your skill
                                set to a certain profession, or enhance your skill set to further your career progression.</p>
                        </div>

                        <div className="col-md-5 bar-grids  w3-agile">
                            <h4> My Hobbies </h4>
                            <p></p>
                            {hobbyComponent}

                            <div className="clearfix"></div>
                            <h4> Programming skills </h4>
                            {programComponent}
                        </div>
                        <div className="clearfix"></div>
                    </div>
                </div>
            </div>
        )
    }
}


export default skillContainer