

import React ,{ Component} from 'react';
import WhatIdo from '../components/whatIdo'
import axios from "axios";

class WorkContainer extends Component {
    state={
        works:null
    }
    componentDidMount() {
            if(!this.state.skills){
                // alert('sdfsd')
                axios.get('https://portfolio-99b6a.firebaseio.com/whatIdo.json')
                    .then((result)=>{
                        this.setState(this.state.works = result.data);

                    })
            }
        }
        render() {

            let workComponet;
            if(this.state.works){
                workComponet = this.state.works.map(
                    (work , index)=>{
                        return <WhatIdo
                            key={index}
                            icon={work.icon}
                            name={work.name}
                            des = {work.description}
                        />
                    }
                )
            }
            return(
                <div className="services text-center pt-5 pb-lg-5" id="what"
                     style={{background: "#e3e3e3", paddingBottom: "20px"}}>
                    <div className="container py-xl-5 py-lg-3">
                        <h3 className="tittle text-center text-bl mb-sm-5 mb-4">What i do ?</h3>
                        <div className="row ser-styles ser-styles-res">
                            {workComponet}
                        </div>
                    </div>
                </div>
            )
        }
}

export default WorkContainer;